/* Colors */
:root {
  --light-yellow: #f3e0a3;
  --gray: #a09e96;
  --light-red: #ff6f61;
  --black-text: #000000;
  --cyan: #00bcd4;
}

/* Dark mode background */
.canvas {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: #1e1e1e; /* Dark background for the canvas */
  user-select: none;
}

/* Debug coordinates */
.debug-coordinates {
  position: absolute;
  top: 10px;
  left: 10px;
  color: white; /* White text for better contrast in dark mode */
  font-size: 16px;
  font-family: Arial, sans-serif;
  z-index: 10; /* Ensure it's always on top */
}

/* Grid container with dots */
.grid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

/* Individual grid dots */
.grid-dot {
  position: absolute;
  width: 6px;
  height: 6px;
  background-color: var(--gray); /* Gray color for grid dots */
  border-radius: 50%;
  opacity: 0.8; /* Slight transparency for subtle look */
}

/* Timeline container */
.timeline {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2; /* Ensure the timeline is above the grid */
  width: 100%;
  height: 2px;
  background-color: var(--cyan); /* Cyan timeline line */
}

/* Future and past timeline lines */
.future-line {
  position: absolute;
  top: -5px;
  width: 100%;
  height: 2px;
  background-color: var(--gray); /* Gray future line */
}

/* Dot styles for events (future and past) */
.line {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--cyan); /* Cyan line */
}

.future-line {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--gray); /* Gray future line */
}
